@import url(https://use.fontawesome.com/releases/v5.13.0/css/all.css);
body {
  margin: 0;
    background-image: url("https://i.pinimg.com/originals/77/0b/c5/770bc50cee014a808e818037164ac7fe.jpg");
    background-size: cover;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.form {
    width: 300px;
    margin: 100px auto;
}

.input_wrapper {
    position: relative;
}

.input_wrapper:before {
    bottom: 0;
    margin: auto 0;
    font-family: "Font Awesome\ 5 Free";
    content: "\f002";
    font-weight: 600;
    color: rgb(75,173,233);
    right: 0;
    top: 12px;
    padding: 0;
    position: absolute;
    pointer-events: none;
}

input[type=text] {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    width: 300px;
    height: 36px;
    border-radius: 20px;
    border-color: white;
    outline: none;
    padding-left: 20px;
}


